package com.jonnyzzz.logo.web

import com.jonnyzzz.logo.script.Script
import com.jonnyzzz.logo.web.components.SvgWorld
import com.jonnyzzz.logo.web.components.scriptEditorComponent
import com.jonnyzzz.logo.web.css.JogoStyle
import com.jonnyzzz.logo.web.i18.Message
import com.jonnyzzz.logo.web.i18.msg
import com.jonnyzzz.logo.web.reducers.JogoState
import com.jonnyzzz.logo.web.reducers.SharePlaneBase64
import com.jonnyzzz.logo.web.reducers.useJogoSelector
import kotlinx.css.*
import react.Props
import react.dom.button
import react.dom.div
import react.fc
import react.useState
import styled.css
import styled.styledDiv
import styled.styledSpan

private fun CssBuilder.mainComponentCss() {
  JogoStyle.html(this)

  child("div.main-content") {
    paddingLeft = 10.px
  }

  child("div.main-header") {
    fontSize = 200.pct
    fontWeight = FontWeight.bold
    paddingLeft = 10.px
  }

  child(".svg") {
    width = 200.px
    height = 200.px
  }

  child(".linkText") {
    width = 200.px
    height = 200.px
  }
}

val JogoShareHeader = Message(
  en = "Share",
  ru = "Поделиться",
  de = "Teilen",
)


val JogoShare = fc<Props> {
  val state: JogoState = useJogoSelector { this }

  //TODO: make it compute the hash lazily
  val serializedToUrl = SharePlaneBase64.generateUrl(state)

  var isCopied by useState { false }

  styledDiv {
    css.mainComponentCss()

    msg(en = "Copy this link and send it to your friends to share your work")

    div(classes = "svg") {
      child(SvgWorld) {
        attrs {
          world = state.world
          script = Script(state.script)
        }
      }
    }

    msg(en = "This is the link to copy, it may be long, no worries!")

    div(classes = "linkText") {
      scriptEditorComponent {
        text = serializedToUrl
        readonly = true
      }
    }

    child(CopyToClipboard::class) {
      attrs.text = serializedToUrl
      attrs.onCopy = { _, _ -> isCopied = true }

      div {
        button {
          +" "
          msg(en = "Copy to Clipboard", de = "Kopieren", ru = "Скопировать")
          +" "
        }

        if (isCopied) {
          styledSpan {
            css { color = JogoStyle.jogoBorderColor }
            +" "
            msg(en = " Copied!", ru = "Скопировано!", de = "Kopiert!")
            +" "
          }
        }
      }
    }
  }
}


