package com.jonnyzzz.logo.web.components

import com.jonnyzzz.logo.web.css.JogoStyle
import kotlinx.css.CssBuilder
import kotlinx.css.height
import kotlinx.css.pct
import kotlinx.css.width
import kotlinx.html.js.onChangeFunction
import org.w3c.dom.HTMLTextAreaElement
import react.Props
import react.RBuilder
import react.RComponent
import react.State
import react.dom.value
import styled.styledTextarea

fun RBuilder.scriptEditorComponent(props: ScriptEditorProps.() -> Unit) {
  child(ScriptEditor::class) {
    attrs {
      text = ""
      onTextChange = {}
      readonly = true
      props()
    }
  }
}

external interface ScriptEditorProps : Props {
  var text: String
  var onTextChange: (String) -> Unit
  var readonly: Boolean?
  var adjustCols: Boolean?
  var extraRows: Int?
}

private class ScriptEditor : RComponent<ScriptEditorProps, State>() {
  private fun CssBuilder.mainComponentCSS() {
    JogoStyle.jetbrainsMono(this)
    width = 100.pct
    height = 100.pct
  }

  override fun RBuilder.render() {
    styledTextarea {
      css.mainComponentCSS()

      val defaultCols = 20
      val defaultRows = 4

      val cols = if (props.adjustCols == true) {
        val actual = props.text.split("\n").map { it.trim().length }.maxOrNull() ?: 0
        (3 + actual).coerceIn(5, defaultCols).toString()
      } else defaultCols

      val rows = if (props.readonly == true) {
        val lines = props.text.count { it == '\n' }
        ((props.extraRows ?: 1) + lines).coerceAtLeast(defaultRows)
      } else defaultRows

      attrs.readonly = props.readonly ?: false
      attrs.rows = rows.toString()
      attrs.cols = cols.toString()
      attrs.value = props.text

      attrs.onChangeFunction = {
        props.onTextChange(it.target.unsafeCast<HTMLTextAreaElement>().value)
      }
    }
  }
}
