package com.jonnyzzz.logo.web.components

import com.jonnyzzz.logo.web.css.JogoStyle.jetbrainsMono
import com.jonnyzzz.logo.web.css.JogoStyle.mainBorder
import com.jonnyzzz.logo.web.reducers.JogoState
import com.jonnyzzz.logo.web.reducers.StateAction
import com.jonnyzzz.logo.web.reducers.useJogoDispatch
import com.jonnyzzz.logo.web.reducers.useJogoSelector
import kotlinext.js.jsObject
import kotlinext.js.jso
import kotlinx.css.*
import org.w3c.dom.HTMLDivElement
import react.*
import styled.styledDiv

private class UpdateText(private val newText: String) : StateAction {
  override fun JogoState.update() = copy(script = newText)
}

val ScriptEditor2 = fc<Props> {
  //just a usage to trick DCEVM
  codeMirrorStyles.toString()

  val script = useJogoSelector { this.script }
  val dispatcher = useJogoDispatch<UpdateText>()

  child<ScriptEditorComponentProps, ScriptEditorComponent> {
    attrs {
      scriptText = script
      onScriptTextChange = { dispatcher(UpdateText(it)) }
    }
  }
}

external interface ScriptEditorComponentProps : Props {
  var scriptText: String
  var onScriptTextChange: (String) -> Unit
}

private class ScriptEditorComponent : RComponent<ScriptEditorComponentProps, State>() {
  private val parentRoot = createRef<HTMLDivElement>()
  private lateinit var codeMirror: CodeMirror

  override fun componentDidMount() {
    codeMirror = CodeMirror(parentRoot.current!!, jso {
      value = props.scriptText
    })

    codeMirror.on("changes") { _: dynamic, _ : dynamic ->
      val newText = codeMirror.getDoc().getValue()
      props.onScriptTextChange(newText)
    }

    codeMirror.focus()
    codeMirror.execCommand("goDocEnd")
  }

  override fun componentDidUpdate(prevProps: ScriptEditorComponentProps, prevState: State, snapshot: Any) {
    val doc = codeMirror.getDoc()
    if (doc.getValue() != props.scriptText) {
      doc.setValue(props.scriptText)
    }
  }

  override fun componentWillUnmount() {
    //CodeMirror does not require that
  }

  private fun CssBuilder.mainComponentCss() {
    mainBorder(this)
    height = 100.pct
    jetbrainsMono(this)
    fontSize = 120.pct

    children(".CodeMirror") {
      height = 100.pct
      overflowY = Overflow.scroll
    }
  }

  override fun RBuilder.render() {
    styledDiv {
      css.mainComponentCss()
      ref = parentRoot
    }
  }
}
