package com.jonnyzzz.logo.web.components

import org.w3c.dom.HTMLElement

val codeMirrorStyles = kotlinext.js.require("codemirror/lib/codemirror.css")

@JsModule("codemirror")
external fun CodeMirror(parentElement: HTMLElement, opts: dynamic): CodeMirror

external interface CodeMirrorDoc {
  fun getValue(): String
  fun setValue(content: String)
}

external interface CodeMirror {
  fun getDoc(): CodeMirrorDoc
  fun on(event: String, handler: dynamic)
  fun off(event: String, handler: dynamic)
  fun focus()
  fun execCommand(cmd: String)
}

